import React, { useState, useEffect } from 'react';
import { 
  MagnifyingGlassIcon, 
  UserIcon, 
  PhoneIcon,
  CheckCircleIcon,
  XMarkIcon
} from '@heroicons/react/24/outline';
import api from '../services/api';
import { useAuth } from '../contexts/AuthContext';

const ContactSelector = ({ onSelectContact, selectedContact, onClear }) => {
  const [contacts, setContacts] = useState([]);
  const [filteredContacts, setFilteredContacts] = useState([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [showContacts, setShowContacts] = useState(false);
  const [loading, setLoading] = useState(false);

  const { user, loading: authLoading } = useAuth();

  useEffect(() => {
    // Wait for auth to complete and a user to be present (token set)
    if (!authLoading && user) {
      fetchContacts();
    }
  }, [authLoading, user]);

  const fetchContacts = async () => {
    try {
      setLoading(true);
      const response = await api.get('/users');
      let users = response.data.users.map(user => ({
        id: user.id,
        name: user.name,
        phone: user.phone,
        email: user.email,
        avatar: null,
        isRegistered: true,
        kycStatus: user.kycStatus
      }));

      // Fallback to demo users if API returns empty (preview domains often miss tokens)
      if ((!users || users.length === 0) && user) {
        const demoUsers = [
          { id: 'priya_rajesh', name: 'Priya Rajesh', phone: '+919000000001', email: 'priya@lendandborrow.com' },
          { id: 'arjun_kumar', name: 'Arjun Kumar', phone: '+919000000002', email: 'arjun@lendandborrow.com' },
          { id: 'suresh_venkatesh', name: 'Suresh Venkatesh', phone: '+919000000003', email: 'suresh@lendandborrow.com' },
          { id: 'meera_patel', name: 'Meera Patel', phone: '+919000000004', email: 'meera@lendandborrow.com' },
          { id: 'rajesh_gupta', name: 'Rajesh Gupta', phone: '+919000000005', email: 'rajesh@lendandborrow.com' },
          { id: 'anita_sharma', name: 'Anita Sharma', phone: '+919000000006', email: 'anita@lendandborrow.com' },
          { id: 'vikram_singh', name: 'Vikram Singh', phone: '+919000000007', email: 'vikram@lendandborrow.com' },
          { id: 'deepika_reddy', name: 'Deepika Reddy', phone: '+919000000008', email: 'deepika@lendandborrow.com' },
          { id: 'rohit_agarwal', name: 'Rohit Agarwal', phone: '+919000000009', email: 'rohit@lendandborrow.com' },
          { id: 'kavya_nair', name: 'Kavya Nair', phone: '+919000000010', email: 'kavya@lendandborrow.com' }
        ];
        users = demoUsers
          .filter(d => d.id !== user.id)
          .map(d => ({ ...d, avatar: null, isRegistered: true, kycStatus: 'PENDING' }));
      }
      
      setContacts(users);
      setFilteredContacts(users);
    } catch (error) {
      console.error('Error fetching contacts:', error);
      if (error?.response?.status === 401) {
        console.warn('Unauthorized when fetching contacts. Please log in again on this domain.');
      }
      // Fallback to demo users if API fails
      if (user) {
        const demoUsers = [
          { id: 'priya_rajesh', name: 'Priya Rajesh', phone: '+919000000001', email: 'priya@lendandborrow.com' },
          { id: 'arjun_kumar', name: 'Arjun Kumar', phone: '+919000000002', email: 'arjun@lendandborrow.com' },
          { id: 'suresh_venkatesh', name: 'Suresh Venkatesh', phone: '+919000000003', email: 'suresh@lendandborrow.com' },
          { id: 'meera_patel', name: 'Meera Patel', phone: '+919000000004', email: 'meera@lendandborrow.com' },
          { id: 'rajesh_gupta', name: 'Rajesh Gupta', phone: '+919000000005', email: 'rajesh@lendandborrow.com' },
          { id: 'anita_sharma', name: 'Anita Sharma', phone: '+919000000006', email: 'anita@lendandborrow.com' },
          { id: 'vikram_singh', name: 'Vikram Singh', phone: '+919000000007', email: 'vikram@lendandborrow.com' },
          { id: 'deepika_reddy', name: 'Deepika Reddy', phone: '+919000000008', email: 'deepika@lendandborrow.com' },
          { id: 'rohit_agarwal', name: 'Rohit Agarwal', phone: '+919000000009', email: 'rohit@lendandborrow.com' },
          { id: 'kavya_nair', name: 'Kavya Nair', phone: '+919000000010', email: 'kavya@lendandborrow.com' }
        ];
        const fallback = demoUsers
          .filter(d => d.id !== user.id)
          .map(d => ({ ...d, avatar: null, isRegistered: true, kycStatus: 'PENDING' }));
        setContacts(fallback);
        setFilteredContacts(fallback);
      } else {
        setContacts([]);
        setFilteredContacts([]);
      }
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    if (searchTerm) {
      const filtered = contacts.filter(contact =>
        contact.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
        contact.phone.includes(searchTerm)
      );
      setFilteredContacts(filtered);
    } else {
      setFilteredContacts(contacts);
    }
  }, [searchTerm, contacts]);

  const handleContactSelect = (contact) => {
    if (contact.isRegistered) {
      onSelectContact(contact);
      setShowContacts(false);
      setSearchTerm('');
    }
  };

  const handleInputFocus = () => {
    setShowContacts(true);
  };

  const handleInputChange = (e) => {
    setSearchTerm(e.target.value);
    setShowContacts(true);
  };

  const handleClear = () => {
    setSearchTerm('');
    setShowContacts(false);
    onClear();
  };

  return (
    <div className="relative">
      {/* Input Field */}
      <div className="relative">
        <input
          type="text"
          value={selectedContact ? selectedContact.name : searchTerm}
          onChange={handleInputChange}
          onFocus={handleInputFocus}
          placeholder="Search contacts or enter phone number"
          className="form-input pr-10"
        />
        {selectedContact && (
          <button
            onClick={handleClear}
            className="absolute inset-y-0 right-0 pr-3 flex items-center"
          >
            <XMarkIcon className="h-5 w-5 text-gray-400 hover:text-gray-600" />
          </button>
        )}
      </div>

      {/* Contact Dropdown */}
      {showContacts && !selectedContact && (
        <div className="absolute z-50 mt-1 w-full bg-white border border-gray-300 rounded-lg shadow-lg max-h-80 overflow-y-auto">
          {loading ? (
            <div className="px-4 py-8 text-center">
              <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-teal-600 mx-auto mb-4"></div>
              <p className="text-gray-500">Loading contacts...</p>
            </div>
          ) : filteredContacts.length > 0 ? (
            <div className="py-2">
              {filteredContacts.map((contact) => (
                <div
                  key={contact.id}
                  onClick={() => handleContactSelect(contact)}
                  className={`px-4 py-3 hover:bg-gray-50 cursor-pointer flex items-center space-x-3 ${
                    !contact.isRegistered ? 'opacity-50 cursor-not-allowed' : ''
                  }`}
                >
                  <div className="flex-shrink-0">
                    {contact.avatar ? (
                      <img
                        src={contact.avatar}
                        alt={contact.name}
                        className="w-10 h-10 rounded-full"
                      />
                    ) : (
                      <div className="w-10 h-10 bg-gradient-to-r from-teal-400 to-teal-600 rounded-full flex items-center justify-center">
                        <span className="text-white font-medium text-sm">
                          {contact.name.charAt(0)}
                        </span>
                      </div>
                    )}
                  </div>
                  
                  <div className="flex-1 min-w-0">
                    <div className="flex items-center space-x-2">
                      <p className="text-sm font-medium text-gray-900 truncate">
                        {contact.name}
                      </p>
                      {contact.isRegistered && (
                        <CheckCircleIcon className="w-4 h-4 text-green-500" />
                      )}
                    </div>
                    <div className="flex items-center space-x-1">
                      <PhoneIcon className="w-3 h-3 text-gray-400" />
                      <p className="text-sm text-gray-500">{contact.phone}</p>
                    </div>
                  </div>

                  {!contact.isRegistered && (
                    <div className="flex-shrink-0">
                      <span className="inline-flex items-center px-2 py-1 rounded-full text-xs font-medium bg-gray-100 text-gray-800">
                        Not on platform
                      </span>
                    </div>
                  )}
                </div>
              ))}
            </div>
          ) : (
            <div className="px-4 py-8 text-center">
              <UserIcon className="w-12 h-12 text-gray-400 mx-auto mb-4" />
              <p className="text-gray-500">No contacts found</p>
              <p className="text-sm text-gray-400 mt-1">
                Try searching with a different name or phone number
              </p>
            </div>
          )}
        </div>
      )}

      {/* Selected Contact Display */}
      {selectedContact && (
        <div className="mt-3 p-3 bg-green-50 border border-green-200 rounded-lg">
          <div className="flex items-center space-x-3">
            <div className="flex-shrink-0">
              {selectedContact.avatar ? (
                <img
                  src={selectedContact.avatar}
                  alt={selectedContact.name}
                  className="w-10 h-10 rounded-full"
                />
              ) : (
                <div className="w-10 h-10 bg-gradient-to-r from-teal-400 to-teal-600 rounded-full flex items-center justify-center">
                  <span className="text-white font-medium text-sm">
                    {selectedContact.name.charAt(0)}
                  </span>
                </div>
              )}
            </div>
            
            <div className="flex-1">
              <div className="flex items-center space-x-2">
                <p className="text-sm font-medium text-gray-900">
                  {selectedContact.name}
                </p>
                <CheckCircleIcon className="w-4 h-4 text-green-500" />
              </div>
              <div className="flex items-center space-x-1">
                <PhoneIcon className="w-3 h-3 text-gray-400" />
                <p className="text-sm text-gray-500">{selectedContact.phone}</p>
              </div>
            </div>

            <button
              onClick={handleClear}
              className="flex-shrink-0 p-1 text-gray-400 hover:text-gray-600"
            >
              <XMarkIcon className="w-5 h-5" />
            </button>
          </div>
        </div>
      )}

      {/* Overlay to close dropdown */}
      {showContacts && (
        <div
          className="fixed inset-0 z-40"
          onClick={() => setShowContacts(false)}
        />
      )}
    </div>
  );
};

export default ContactSelector;
